}
.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}

/* blog previews / list */
.blog-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
}
.blog-preview:hover {
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}
.blog-preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}
.blog-preview a{
  text-decoration: none;
}

/* blog details page */
.blog-details h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 10px;
  text-align: center;
}
.blog-details div {
  margin: 20px 0;
  font-size: 14px;
}
.blog-details button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

/* create new blog form */
.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label {
  text-align: left;
  display: block;
}
.create h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}
.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

.create .addedu{
  display: block;
  font-weight: 900;
  color: rgb(56, 53, 53);


}


.create .addbtn{
  float: right;
  font-size: 30px;
  margin-top: -26px;
  cursor: pointer;
  color: rgb(56, 53, 53);
}
.create .savebtn{
  display: block;
  margin-top: 50px;
}

.create .eduback{
  font-weight: bold;
  color: rgb(56, 53, 53);
}
.create .enddate{
  margin-bottom: 26px;
}

.addexpbtn{
  float: right;
  font-size: 30px;
  margin-top: -26px;
  cursor: pointer;
  color: rgb(56, 53, 53);
}

.expert .describeinp{
  margin-bottom: 26px;
}
.create .addexp{
  font-weight: 900;
  margin-top: 10px;
  color: rgb(56, 53, 53);
}

